@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.container {
  background-size: cover;
  -moz-background-size: cover;
  -webkit-background-size: cover;
  height: 100%;
  margin-right: 16px;
}
.topbox {
  background: #303030;
  background: #303030; /* Old browsers */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0f161d', endColorstr='#0f161d',GradientType=1 ); /* IE6-9 */
}

.rollbox {
  height: 103px;
  overflow-x: auto;
  overflow: hidden;
  position: relative;
  padding: 0;
}
.rollbox > table {
  width: auto;
  margin: 0;
  padding: 0;
}
.loadout {
  position: absolute;
  top: 5px;
  left: 5px;
  z-index: 1;
  background: #303030;
}

.roller {
  position: relative;
  display: block;
  text-align: center;
  color: white;
  font-size: 20px;
  background: #303030;
  overflow-wrap: break-word;
}
.roller div {
  display: block;
  height: 50px;
  position: absolute;
  bottom: 10px;
  width: 100%;
  left: 0;
  text-transform: uppercase;
  font-family: "Roboto";
  font-weight: normal;
}

.badge {
  padding-top: 5px;
  text-shadow: 1px 1px 1px black;
  margin-bottom: 20px;
}
.line {
  width: 2px;
  height: 198px;
  top: 1px;
  left: 50%;
  position: absolute;
  background: #57b3f9;
  opacity: 0.6;
  z-index: 2;
}
.log {
  text-align: center;
  font-size: 24px;
  color: #ff3d42;
  font-weight: bold;
  font-family: "Roboto";
  text-transform: uppercase;
}
.log span {
  font-size: 24px;
  font-weight: bold;
  color: #57b3f9;
  font-family: "Roboto";
  text-transform: uppercase;
}
.roll {
  margin-top: 15px;
}
.roller {
  height: 90px;
  background: rgb(255, 61, 66);
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(255, 61, 66, 0.6222864145658263)),
    color-stop(50%, rgba(255, 61, 66, 1)),
    to(rgba(255, 61, 66, 0.6166841736694677))
  );
  background: linear-gradient(
    90deg,
    rgba(255, 61, 66, 0.6222864145658263) 0%,
    rgba(255, 61, 66, 1) 50%,
    rgba(255, 61, 66, 0.6166841736694677) 100%
  );
  width: 180px;
  margin-right: 10px;
  box-shadow: 0 0 5px 0 #ff3d42;
}
tr,
table,
td {
  margin: 0;
  padding: 0;
}

#prev-names div {
  padding: 0 4px 0 4px;
  height: 80%;
  background: white;
  border-radius: 5px;
  color: black;
  text-align: center;
  margin: 2px 5px 0 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-transform: uppercase;
  font-family: "Roboto";
  word-wrap: none;
  word-break: keep-all;
}

#prev-names {
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
  overflow-y: hidden;
  right: 40px;
  margin-right: 60px;
}

.hideScrollBar::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE and Edge */
.hideScrollBar {
  -ms-overflow-style: none;
}

.header-text {
  text-align: center;
  color: #ff3d42;
  font-family: "Roboto";
}
#AppBar {
  height: "26px";
  margin: "auto";
  background: rgb(255, 61, 66);
  background: #303030;
}

.infoText {
  color: aqua;
  text-align: center;
  font-family: "Roboto";
}

.settings {
  background-color: #ff3d42;
}

